body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    background-color: rgb(221, 215, 233);
    color: #061257;
    height: 85px;
    padding: 20px;
    position: fixed;
    top: 0;
    align-items: center;
    width: 100%;
}

.header-title {
    font-size: 3em;
    margin-left: 30px;
    font-family: 'Modern Antiqua', cursive;
    
}
.header-img {
    width: 145px;
    margin-top: 50px;
}

   @media (max-width: 540px) {
    .header-title {
        font-size: 2em;
        margin-left: 15px;
        width: 63%;
    }
    .header-img {
        width: 125px;
        margin-top: 50px;
    }
   }
.movie-container {
    border: 1px solid  #e2d7d7;
    text-align: center;
    background-color: #efefef;
    color: rgb(39, 24, 24);
    width: 300px;
    height: 520px;
    min-height: 200px;
    margin: 20px;
    padding: 10px;
    justify-content: space-between;
    font-family: 'Ubuntu', sans-serif;
    box-shadow: 1px 1px 20px 2px #4e4d4d;

}
.bold {
    font-weight: bold;
}
.director {
    height: 35px;
}
.a-title {
    height: 55px;
    display: block;
}

.title {
    color: #837090;
    text-transform: capitalize;
}

.preview-body {
    justify-items: center;
}
.a-img {
    width: 70%;
    margin: 10px auto;
    height: 300px;
    box-shadow: 5px 3px 7px 1px rgba(98, 98, 132, 0.5);
}

.year-container {
    margin: 10px auto;
}

.genre-list {
    font-size: 0.85em;
    padding: 0;
    overflow-x: auto;
    margin-bottom: 10px;
}
.ul-genre {
    margin: auto;
    padding: 0;
}
.genre {
    border: 1px solid #dcc3c3;
    list-style-type: none;
    padding: 4px;
    margin: 0 5px;
    border-radius: 4px;
    min-width: 50px;
}

@media (max-width: 540px) {
    width: 85%;
}

.movies {
    font-family: 'Ubuntu', sans-serif;
}
.user-container {
    align-items: center;
    display: flex;
    flex-direction: column;
    margin: 50px;
}

.movie-list {
    text-decoration: none;
    list-style-type: none;
}

.movies-list {
    flex-wrap: wrap;
    justify-content: center;
    padding-left: 0;
}

.sub-title {
    width: 100%;
    height: 70px;
    background-color: #c6a5f7;
    align-items: center;
    justify-content: center;
}

.sub-title>p {
    font-size: 2em;
    margin-right: 20px;
    /* margin-top: 10px; */
    color: #061257;
    text-transform: capitalize;
}

.add-btn, .search-btn {
    margin: 0 5px;
    font-size: 1.2em;
}
.next-btn {
    margin-right: 30px;
}
.next-arrow {
    justify-content: center;
}
.plus-add, .search {
    justify-content: center;
}

.plus-add>span {
    margin-right: 5px;
    font-weight: bold;
}

.search-line {
    height: 30px;
}
.movie-preview {
    margin: auto;
}

@media (max-width: 750px) {
    .sub-title {
        padding: 10px;
    }
    .sub-title>p {
        font-size: 1.6em;
    }
}
@media (max-width: 670px) {
    .sub-title {
        flex-direction: column;
        height: 125px;
        align-items: center;
    }
    .sub-title>p {
        font-size: 1.5em;
        margin-left: 15px;
    }
    .serach-container {
        margin-bottom: 10px;
    }
}

.edit {
    margin: auto;
    font-family: 'Ubuntu', sans-serif;
}

.form-edit {
    width: 50%;
    margin: auto;
    border: 1px solid;
    padding: 10px;
    background-color: rgb(221, 215, 233, 0.5);
}
.input-edit {
    margin: 10px;
    height: 30px;
    width: 350px;
    padding: 5px;
    justify-items: flex-start;
}

.edit-title {
    text-align: center;
}

.input-container {
    align-items: center;
    justify-content: flex-end; 
    margin-right: 40px;
}

.input-container>p {
    font-weight: bold;
    margin-right: 20px;
}

.btns {
    margin: 0 auto;
    justify-content: space-around;
}

.btn {
    align-self: center;
    /* margin-top: 10px; */
    background-color: #ddd7e9;
    color: #4a4646;
    padding: 7px 5px 5px 5px;
    border-radius: 3px;
    font-size: 1em;
    width: 80px;
    height: 34px;
    box-shadow: 4px 3px 7px 0px #818181;
}

.btn:hover {
    -webkit-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
}

@media (max-width: 980px) {
    .form-edit {
        width: 60%;
    } 
}

@media (max-width: 820px) {
    .form-edit {
        width: 70%;
    } 
}

@media (max-width: 720px) {
    .form-edit {
        width: 85%;
    } 
}

@media (max-width: 600px) {
    .input-container {
        flex-direction: column;
        height: 80px;
        margin-right:0;
    } 
    .input-edit {
        width: 85%;
    }
}
.movie-details {
    border: 1px solid #e2d7d7;
    box-shadow: 1px 1px 20px 2px #4e4d4d;
    text-align: center;
    background-color: #efefef;
    color: rgb(39, 24, 24);
    width: 500px;
    height: 350px;
    min-height: 200px;
    margin: 20px auto;
    padding: 10px;
    justify-content: space-around;
    font-family: 'Ubuntu', sans-serif;
}

.details-container {
    justify-content: space-around;
}
.details-container>img {
    width: 180px;
    height: 275px;
    margin-right: 15px;
    box-shadow: 10px 3px 7px 1px rgba(98, 98, 132, 0.5)
}
.title-details {
    margin: auto;
    font-family: 'Ubuntu', sans-serif;
    text-align: center;
    font-size: 3em;
}

.movie-text {
    justify-content: space-between;
}
.mb15 {
    margin-bottom: 15px;
}
.ml15 {
    margin-left: 15px;
}
.plot {
    margin: 10px;
}

@media (max-width: 600px) {
    .details-container {
        flex-direction: column;
    }
    .details-container>img {
        width: 200px;
        height: 250px;
        margin: 10px auto;
    }
    .movie-details {
        height: 600px;
        width: 85%;
    }
    .title-details {
        font-size: 1.5em;
        margin-top: 10px
    }
}
.body {
  text-align: center;
  font-family: 'Ubuntu', sans-serif;
}

p, h1,h2, h3, h4, h5, h6, li, body{
  margin: 0;
  padding: 0;
  text-decoration: none;
}
body {
  margin-top: 150px
}
a {
  text-decoration: none;
}
a:visited {
  color: inherit;
}
.flex {
  display: flex;
}

.column {
  flex-direction: column;
}

.btn {
  cursor: pointer;
}
.body::child {
  margin-top: 250px;
}
